<template>
  <div class="business-portal-page">
    <div class="page-desc">{{ $t('IDC.bankAccount.pageDesc') }}</div>
    <div class="table-list-container">
      <div class="tab-container">
        <div class="tab-box">
          <el-button
            :class="['tab-btn', activeName === 'first' ? 'active' : 'inactive']"
            type="text"
            @click="activeBtnClick()"
          >
            {{ $t('IDC.bankAccount.label.activeAccount') }}
          </el-button>
          <el-button
            :class="['tab-btn', activeName === 'second' ? 'active' : 'inactive']"
            type="text"
            @click="removedBtnClick()"
          >
            {{ $t('IDC.bankAccount.label.removedAccount') }}
          </el-button>
        </div>
      </div>
      <div v-if="activeName === 'first' && identityId">
        <transition name="el-fade-in-linear">
          <ActiveAccountContent
            ref="activeAccountContent"
            v-show="activeName === 'first'"
            :identity-id="identityId"
            :searchForm="searchForm"
            :assetTypeOptions="assetTypeOptions"
            :bankHeadOfficeOptions="bankHeadOfficeOptions"
          ></ActiveAccountContent>
        </transition>
      </div>
      <div v-if="activeName === 'second' && identityId">
        <transition name="el-fade-in-linear">
          <RemovedAccountContent
            ref="removedAccountContent"
            v-show="activeName === 'second'"
            :identity-id="identityId"
            :searchForm="searchForm"
            :assetTypeOptions="assetTypeOptions"
            :bankHeadOfficeOptions="bankHeadOfficeOptions"
          ></RemovedAccountContent>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import { queryDict, getBankHeadOfficeList } from '@/views/identifier-center/api/api-bank-account.js';
import ActiveAccountContent from '@/views/identifier-center/pages/bank-account/components/active-account-content';
import RemovedAccountContent from '@/views/identifier-center/pages/bank-account/components/removed-account-content';

export default {
  name: 'BankAccountList',
  components: {
    ActiveAccountContent,
    RemovedAccountContent,
  },
  props: {
    identityId: {
      require: true,
      type: String,
    },
  },
  data() {
    return {
      searchForm: {
        assetType: null,
        accountName: null,
        bankHeadOffice: null,
        bankName: null,
      },
      bankHeadOfficeOptions: [
        {
          value: null,
          label: 'All',
        },
      ],
      assetTypeOptions: [
        {
          value: null,
          label: 'All',
        },
      ],
      activeName: 'first',
    };
  },
  computed: {},
  watch: {},
  created() {
    this.getAssetTypeList();
    this.getBankHeadOfficeList();
  },
  activated() {},
  mounted() {},
  methods: {
    activeBtnClick() {
      this.activeName = 'first';
    },
    removedBtnClick() {
      this.activeName = 'second';
    },
    async getBankHeadOfficeList() {
      let res = await getBankHeadOfficeList({});
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.jsonDataStr);
        dataDictJson.map((item) => {
          let officeObj = {
            value: item.code,
            label: item.value,
          };
          this.bankHeadOfficeOptions.push(officeObj);
        });
      }
    },
    async getAssetTypeList() {
      let res = await queryDict({ dictType: 'AssetType' });
      if (res.header && res.header.success) {
        let dataDictJson = JSON.parse(res.dataDictJson);
        dataDictJson.map((item) => {
          let assetTypeObj = {
            value: item.code,
            label: item.name,
          };
          this.assetTypeOptions.push(assetTypeObj);
        });
      }
    },
  },
};
</script>

