  color: $--text_color--secondary;
  font-size: 12px;
  margin: 20px 20px 0 20px;
}

.tab-container {
  display: flex;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 20px;
  .tab-box {
    display: flex;
    align-items: center;
    padding: 4px;
    border-radius: 4px;
    background-color: $--fill_color--regular;

    .tab-btn {
      background-color: $base-menu-light-background;
      width: 130px;
      height: 32px;
      border-radius: 4px;
      font-size: 14px;
    }
    .active {
      background-color: $base-menu-light-background;
      color: $--primary_color;
    }

    .inactive {
      background-color: $--fill_color--regular;
      color: $--text_color--strong;
    }
  }
}
</style>
