import request from '@/common/utils/request';
import { buildUrlWithObj } from '@/common/utils/url-utils';

// 获取组织操作员信息
export function queryOrganizationDetail(data) {
  return request({
    url: buildUrlWithObj('/fintech/v1/organization/detail', data),
    method: 'get',
  });
}

// 获取组织操作员状态列表
export function queryOrganizationStatusList(data) {
  return request({
    url: buildUrlWithObj('/fintech/v1/organization/status', data),
    method: 'get',
  });
}

// 修改操作员状态
export function changeOrganizationStatus(data) {
  return request({
    url: '/fintech/v1/organization/changeStatus',
    data: data,
    method: 'post',
  });
}
