<template>
  <div class="org-account-contianer">
    <portal-header :projectObj="titleConfig" :showBottomLine="true"></portal-header>
    <!--search -->
    <portal-form
      ref="searchForm"
      :form-data="searchFormData"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      @common-enter-event="search"
      @reset-btn-click="reset"
      @search-btn-click="search"
    />
    <!-- columns -->
    <div class="table-list-container">
      <div style="padding-bottom: 16px">
        <el-dropdown @command="exportFile">
          <el-button
            type="primary"
            size="small"
            :disabled="disAbledExportBtn || !hasPerm('IC_ORG_ACCOUNTINFO_EXPORT_BTN')"
            v-preventReClick
          >
            {{ $t('common.button.export') }}
            <i class="el-icon-arrow-down el-icon--right" style="font-size: 12px"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="XLS">Excel</el-dropdown-item>
            <el-dropdown-item command="XLSX">Excel 2007</el-dropdown-item>
            <el-dropdown-item command="CSV">CSV</el-dropdown-item>
            <el-dropdown-item command="PDF">PDF</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <table-page
        size="small"
        class="table-page"
        :has-pagination="true"
        :columns="columns"
        :initializingLoading="false"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :is-search="isSearch"
      >
        <template slot="status" slot-scope="scope">
          <div>
            <span class="content-item">{{ scope.data.row.status }}</span>
            <el-link
              :disabled="!hasPerm('IC_CHANGEORGACCTSTATUS_BTN')"
              :underline="false"
              icon="el-icon-edit"
              @click="showEditStatusDialog(scope.data.row)"
            ></el-link>
          </div>
        </template>
        <template slot="ruleProfile" slot-scope="scope">
          <div class="edit-cell">
            <el-tooltip
              class="item"
              effect="dark"
              :disabled="!scope.data.row.ruleProfileName"
              :content="scope.data.row.ruleProfileName"
              placement="top"
            >
              <div class="cell el-tooltip">{{ scope.data.row.ruleProfileName || '-' }}</div>
            </el-tooltip>
            <el-link
              class="edit-text el-icon-edit"
              :underline="false"
              :disabled="!hasPerm('IC_CHANGEORGACCTRULEPROFILE_BTN')"
              @click="showruleProfileDialog(scope.data.row)"
            ></el-link>
          </div>
        </template>
        <template slot-scope="scope" slot="status">
          <div class="flex flex-align">
            <span class="status-circle" :style="statusStyle(scope.data.row)"></span>
            <span>{{ scope.data.row.status }}</span>
            <el-link
              :underline="false"
              :disabled="!hasPerm('IC_CHANGEORGACCTSTATUS_BTN')"
              icon="el-icon-edit"
              @click="showEditStatusDialog(scope.data.row)"
            ></el-link>
          </div>
        </template>
      </table-page>
    </div>
    <!-- edit-status-dialog -->
    <el-dialog
      :title="this.$t('IDC.orgAccount.editStatusDialogTitle')"
      class="edit-status-dialog"
      width="448px"
      :visible.sync="editStatusDialogVisible"
      :close-on-click-modal="false"
      :before-close="handleEditCancelClick"
      @closed="handleEditCancelClick"
    >
      <portal-form
        ref="editStatusForm"
        class="edit-status-form"
        :needOperateBtn="false"
        :bigAutoColumn="1"
        :smallAutoColumn="1"
        :formRules="searchFormRules"
        :form-data="editStatusFormData"
        :needBottomLine="false"
        :form-config-list="editFormConfigList"
      >
        <template slot="currentStatus">
          <el-form-item :label="editFormConfigList[0].label">
            <span>{{ currentStatus || '-' }}</span>
          </el-form-item>
        </template>
      </portal-form>
      <div slot="footer">
        <button size="small" class="cancel-button" @click.prevent="handleEditStatusCancelClick">
          {{ this.$t('common.button.cancel') }}
        </button>
        <el-button
          type="primary"
          size="small"
          :loading="editStatusSubmitLoading"
          v-preventReClick
          @click="handleEditStatusConfirmClick"
        >
          {{ this.$t('common.button.submit') }}
        </el-button>
      </div>
    </el-dialog>
    <!-- edit-rule-profile-dialog -->
    <el-dialog
      :title="this.$t('IDC.orgAccount.editRuleProfileDialogTitle')"
      class="edit-status-dialog"
      width="448px"
      :visible.sync="editRuleProfileDialogVisible"
      :close-on-click-modal="false"
      :before-close="handleRuleCancelClick"
      @closed="handleRuleCancelClick"
    >
      <portal-form
        ref="ruleProfileForm"
        class="edit-status-form"
        :needOperateBtn="false"
        :needBottomLine="false"
        :bigAutoColumn="1"
        :smallAutoColumn="1"
        :formRules="ruleProfileFormRules"
        :form-data="ruleProfileFormData"
        :form-config-list="ruleProfileFormConfigList"
      >
        <template slot="currentRuleProfile">
          <el-form-item :label="ruleProfileFormConfigList[0].label">
            <span>{{ currentRuleProfile || '-' }}</span>
          </el-form-item>
        </template>
      </portal-form>
      <div slot="footer">
        <button size="small" class="cancel-button" @click.prevent="handleRuleProfileCancelClick">
          {{ this.$t('common.button.cancel') }}
        </button>
        <el-button
          type="primary"
          size="small"
          v-preventReClick
          :loading="ruleProfileSubmitLoading"
          @click="handleRuleProfileConfirmClick"
        >
          {{ this.$t('common.button.submit') }}
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import { exportUtils } from '@/common/utils/export-utils';
import {
  getAccountTypeInfo,
  queryAccount,
  modifyAccount,
  getRuleProfile,
  exportAccountInfo,
} from '@/views/identifier-center/api/api-account.js';
export default {
  name: 'OrgAccount',
  components: { PortalHeader, TablePage, PortalForm },
  props: {
    identityId: {
      type: String,
      require: true,
    },
    isTopOrganization: {
      type: Boolean,
      require: true,
    },
  },
  data() {
    return {
      titleConfig: {
        title: '',
        desc: this.$t('IDC.orgAccount.pageDesc'),
      },
      searchFormData: {
        accountNumber: '',
        accountName: '',
        accountType: '',
        status: '',
      },
      tempOptAccountNo: '',
      accountTypeInfo: [],
      searchFormRules: {
        newStatus: [{ required: true, message: 'Please select at least one.', trigger: 'change' }],
        remark: [{ required: true, message: 'This field is mandatory.', trigger: 'change' }],
      },
      ruleProfileFormData: {
        currentRuleProfile: '',
        ruleProfile: '',
        remark: '',
      },
      ruleProfile: [],
      ruleProfileFormRules: {
        ruleProfile: [{ required: true, message: 'Please select at least one.', trigger: 'change' }],
        remark: [{ required: true, message: 'This field is mandatory.', trigger: 'change' }],
      },
      searchLoading: false,
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      isSearch: false,
      editStatusDialogVisible: false,
      editRuleProfileDialogVisible: false,
      editStatusFormData: {
        currentStatus: '',
        newStatus: '',
        remark: '',
      },
      currentStatus: '',
      currentRuleProfile: '',
      dialogNewStatus: [],
      editStatusSubmitLoading: false,
      ruleProfileSubmitLoading: false,
      statusList: [
        {
          label: 'All',
          value: '',
        },
        {
          label: 'Active',
          value: 'Active',
        },
        {
          label: 'Closed',
          value: 'Closed',
        },
        {
          label: 'Frozen',
          value: 'Frozen',
        },
        {
          label: 'Suspended',
          value: 'Suspended',
        },
      ],
      disAbledExportBtn: true,
    };
  },
  computed: {
    formConfigList() {
      return [
        {
          label: this.$t('IDC.orgAccount.accountNumber'),
          key: 'accountNumber',
          prop: 'accountNumber',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('IDC.orgAccount.accountName'),
          key: 'accountName',
          prop: 'accountName',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('IDC.orgAccount.accountType'),
          key: 'accountType',
          prop: 'accountType',
          comp: 'el-select',
          options: this.accountTypeInfo,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          label: this.$t('IDC.orgAccount.status'),
          key: 'status',
          prop: 'status',
          comp: 'el-select',
          options: this.statusList,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
      ];
    },
    editFormConfigList() {
      return [
        {
          label: this.$t('IDC.orgAccount.currentStatus'),
          key: 'currentStatus',
          prop: 'currentStatus',
          slotName: 'currentStatus',
        },
        {
          label: this.$t('IDC.orgAccount.newStatus'),
          key: 'newStatus',
          prop: 'newStatus',
          comp: 'el-select',
          options: this.dialogNewStatus,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          formItemClass: 'dialog-remark',
          label: this.$t('IDC.orgAccount.remark'),
          key: 'remark',
          prop: 'remark',
          comp: 'el-input',
          bind: {
            type: 'textarea',
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
      ];
    },
    ruleProfileFormConfigList() {
      return [
        {
          label: this.$t('IDC.orgAccount.currentRuleProfile'),
          key: 'currentRuleProfile',
          prop: 'currentRuleProfile',
          slotName: 'currentRuleProfile',
        },
        {
          label: this.$t('IDC.orgAccount.ruleProfile'),
          key: 'ruleProfile',
          prop: 'ruleProfile',
          comp: 'el-select',
          options: this.ruleProfile,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          formItemClass: 'dialog-remark',
          label: this.$t('IDC.orgAccount.remark'),
          key: 'remark',
          prop: 'remark',
          comp: 'el-input',
          bind: {
            type: 'textarea',
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
      ];
    },
    columns() {
      return [
        {
          label: this.$t('IDC.orgAccount.accountNo'),
          prop: 'accountNo',
          align: 'left',
          'min-width': '150',
        },
        {
          label: this.$t('IDC.orgAccount.accountName'),
          prop: 'accountName',
          align: 'left',
          'min-width': '150',
        },
        {
          label: this.$t('IDC.orgAccount.accountType'),
          prop: 'accountTypeName',
          align: 'left',
          'min-width': '150',
        },
        {
          label: this.$t('IDC.orgAccount.status'),
          prop: 'status',
          align: 'left',
          'min-width': '170',
          __slotName: 'status',
        },
        {
          label: this.$t('IDC.orgAccount.currency'),
          prop: 'currency',
          align: 'left',
          'min-width': '120',
        },
        {
          label: this.$t('IDC.orgAccount.alias'),
          prop: 'accountAlias',
          align: 'left',
          'min-width': '150',
        },
        {
          label: this.$t('IDC.orgAccount.accountRelationship'),
          prop: 'relationship',
          align: 'left',
          'min-width': '150',
        },
        {
          label: this.$t('IDC.orgAccount.ruleProfile'),
          prop: 'ruleProfileName',
          align: 'left',
          'min-width': '120',
          showOverflowTooltip: false,
          __slotName: 'ruleProfile',
        },
        {
          label: this.$t('IDC.orgAccount.currentBalance'),
          prop: 'balance',
          align: 'right',
          'min-width': '150',
        },
        {
          label: this.$t('IDC.orgAccount.availableBalance'),
          prop: 'availableBalance',
          align: 'right',
          'min-width': '150',
        },
        {
          label: this.$t('IDC.orgAccount.reservedBalance'),
          prop: 'reservedBalance',
          align: 'right',
          'min-width': '150',
        },
        {
          label: this.$t('IDC.orgAccount.unclearBalance'),
          prop: 'unclearBalance',
          align: 'right',
          'min-width': '150',
        },
      ];
    },
  },
  created() {
    this.getAccountTypeList();
  },
  watch: {
    identityId() {
      if (this.identityId) {
        this.isSearch = true;
      }
    },
  },
  methods: {
    statusStyle(row) {
      switch (row.status) {
        case 'Active':
          return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
        case 'Suspended':
          return { 'background-color': `${this.GlobalStyleVariables.statusColorSAecondaryWarning}` };
        case 'Frozen':
          return { 'background-color': `${this.GlobalStyleVariables.statusColorSAecondaryWarning}` };
        case 'Closed':
          return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
        default:
          break;
      }
    },
    search() {
      this.searchLoading = true;
      this.isSearch = true;
    },
    reset() {
      this.$refs.searchForm.resetFields();
      this.isSearch = true;
    },
    async getTableData() {
      let params = {
        dataScope: ['ALL'],
        accountNo: this.searchFormData.accountNumber,
        accountName: this.searchFormData.accountName,
        accountTypeId: this.searchFormData.accountType,
        status: this.searchFormData.status,
        identityId: this.identityId,
      };
      let res = await queryAccount(params);
      this.isSearch = false;
      this.searchLoading = false;
      if (res.header && res.header.success) {
        if (res.list.length > 0) {
          this.disAbledExportBtn = false;
        } else {
          this.disAbledExportBtn = true;
        }
        return res;
      }
    },
    async getAccountTypeList() {
      let res = await getAccountTypeInfo();
      if (res.header && res.header.success) {
        this.accountTypeInfo = res.list.map((item) => {
          return {
            label: item.accountTypeName,
            value: item.accountTypeId,
          };
        });
      }
    },
    exportFile(fileType) {
      let params = {
        identityId: this.identityId,
        accountNo: this.searchFormData.accountNumber,
        dataScope: ['All'],
        accountName: this.searchFormData.accountName,
        accountTypeId: this.searchFormData.accountType,
        status: this.searchFormData.status,
        fileType: fileType,
      };
      let fileName = 'account_' + Date.parse(new Date());
      exportUtils(exportAccountInfo, params, { fileType, fileName });
    },
    handleEditCancelClick() {
      this.editStatusDialogVisible = false;
    },
    handleRuleCancelClick() {
      this.editRuleProfileDialogVisible = false;
    },
    showEditStatusDialog(val) {
      this.tempOptAccountNo = val.accountNo;
      this.currentStatus = val.status;
      if (val.status === 'Active') {
        this.dialogNewStatus = [
          {
            label: 'Frozen',
            value: 'Frozen',
          },
          {
            label: 'Suspended',
            value: 'Suspended',
          },
        ];
      } else if (val.status === 'Frozen') {
        this.dialogNewStatus = [
          {
            label: 'Active',
            value: 'Active',
          },
          {
            label: 'Suspended',
            value: 'Suspended',
          },
        ];
      } else if (val.status === 'Suspended') {
        this.dialogNewStatus = [
          {
            label: 'Active',
            value: 'Active',
          },
          {
            label: 'Frozen',
            value: 'Frozen',
          },
        ];
      }
      this.editStatusDialogVisible = true;
    },
    async showruleProfileDialog(val) {
      this.editRuleProfileDialogVisible = true;
      this.currentRuleProfile = val.ruleProfileName;
      let tempRuleProfile = await this.getRuleProfileList(val.accountTypeId);
      this.ruleProfile = tempRuleProfile.filter((item) => {
        return item.label !== val.ruleProfileName;
      });
      this.tempOptAccountNo = val.accountNo;
    },
    async getRuleProfileList(id) {
      let identitySubType = this.isTopOrganization ? '5001' : '5002';
      let params = {
        identityType: '5000',
        identitySubType,
        accountTypeId: id,
      };
      let res = await getRuleProfile(params);
      if (res.header && res.header.success) {
        return res.list.map((item) => {
          return {
            label: item.ruleProfileName,
            value: item.id,
          };
        });
      }
    },
    handleEditStatusCancelClick() {
      this.$refs.editStatusForm.clearValidate();
      this.$refs.editStatusForm.resetFields();
      this.editStatusDialogVisible = false;
    },
    handleRuleProfileCancelClick() {
      this.$refs.ruleProfileForm.clearValidate();
      this.$refs.ruleProfileForm.resetFields();
      this.editRuleProfileDialogVisible = false;
    },
    handleEditStatusConfirmClick() {
      this.$refs.editStatusForm.validate((result) => {
        if (result) {
          this.editStatusSubmitLoading = true;
          let params = {
            modifyOpTypes: ['MODIFY_STATUS'], // 固定写死
            account: {
              accountNo: this.tempOptAccountNo,
              status: this.editStatusFormData.newStatus,
              identityId: this.identityId,
            },
            header: {
              remark: this.editStatusFormData.remark,
            },
          };
          modifyAccount(params).then((res) => {
            this.editStatusSubmitLoading = false;
            if (res.header && res.header.success) {
              this.$refs.editStatusForm.resetFields();
              this.$modal.msgSuccess('Operation success.');
              this.isSearch = true;
              this.editStatusDialogVisible = false;
            } else {
              this.$modal.msgError('Operation fail.');
            }
          });
        }
      });
    },
    handleRuleProfileConfirmClick() {
      this.$refs.ruleProfileForm.validate((result) => {
        if (result) {
          this.ruleProfileSubmitLoading = true;
          let params = {
            modifyOpTypes: ['MODIFY_RULE_PROFILE'], // 固定写死
            account: {
              accountNo: this.tempOptAccountNo,
              ruleProfileId: this.ruleProfileFormData.ruleProfile,
              identityId: this.identityId,
            },
            header: {
              remark: this.ruleProfileFormData.remark,
            },
          };
          modifyAccount(params).then((res) => {
            this.ruleProfileSubmitLoading = false;
            if (res.header && res.header.success) {
              this.$refs.ruleProfileForm.resetFields();
              this.$modal.msgSuccess('Operation success.');
              this.editRuleProfileDialogVisible = false;
              this.isSearch = true;
            } else {
              this.$modal.msgError('Operation fail.');
            }
          });
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.org-account-contianer {
  .page-header {
    border: none;
  }
  .form-flex-container {
    padding-top: 0;
  }
}
/deep/ .el-button--text {
  color: $--fill_color--strong;
}

.edit-status-form {
  padding: 0;
}
.cancel-button {
  padding: 7px 15px;
  height: 32px;
  border-radius: 4px;
  background-color: $base-menu-background;
  border: 1px solid $--line_color--regular;
  font-size: 14px;
  color: $--text_color--strong;
  box-sizing: border-box;
  cursor: pointer;
  margin-right: 8px;
}
.edit-status-form {
  padding-top: 20px;
  /deep/ .dialog-remark {
    .el-form-item__content {
      height: 100%;
    }
  }
}
.edit-cell {
  .cell {
    padding-left: 0 !important;
    padding-right: 20px;
  }
  .edit-text {
    position: absolute;
    padding: 0 12px;
    right: 0;
    top: 12px;
    line-height: inherit;
    height: auto;
    cursor: pointer;
    &:hover {
      color: $--primary_color;
    }
  }
}
.edit-status-dialog {
  /deep/ .el-dialog__header {
    padding-bottom: 0;
  }
  /deep/ .edit-status-form {
    padding-top: 20px !important;
  }
}
</style>
