<template>
  <div class="role-config business-portal-page">
    <portal-header :projectObj="titleConfig" :showBottomLine="true"></portal-header>
    <!--search -->
    <portal-form
      ref="searchList"
      :form-data="searchList"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      :formRules="queryFormRules"
      @common-enter-event="search"
      :searchBtnDisabled="
        !hasPerm(activeName === 'orgOperator' ? 'IC_OPERATOR_ORG_SEARCH_BTN' : 'IC_TACKCALL_ORG_SEARCH_BTN ')
      "
      @reset-btn-click="reset"
      @search-btn-click="search"
    />
    <!-- columns -->
    <div class="table-list-container">
      <div class="btn-region">
        <div class="create-btn">
          <el-button
            type="primary"
            size="small"
            icon="el-icon-plus"
            :disabled="!hasPerm('IC_OPERATOR_ORG_ADD_BTN')"
            @click="addOrgOperator"
          >
            {{ $t('common.button.create') }}
          </el-button>
        </div>
        <div class="right-btn" v-if="activeName !== 'organization' && activeName !== 'myOrganization'">
          <el-select
            class="selectSort"
            v-if="sortCode !== ''"
            v-model="sortDataCode"
            placeholder="Please Select"
            size="small"
            style="width: 200px"
            @change="handleCommandSort"
          >
            <el-option v-for="item in sort" :key="item.value" :label="item.label" :value="item.value"> </el-option>
          </el-select>
          <el-select
            v-model="sortCode"
            placeholder="Sort by..."
            style="width: 200px"
            @change="handleCommand"
            size="small"
          >
            <el-option v-for="item in todoInfoList.sortData" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <table-page
        size="small"
        class="table-page"
        :has-pagination="true"
        :columns="columns"
        :initializingLoading="false"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :is-search="refreshTableFlag"
        :sortArr="activeName == 'orgOperator' ? sortArr : []"
      >
        <template slot="status" slot-scope="scope">
          <div class="edit-button-container">
            <span
              v-if="scope.data.row.status !== ''"
              class="status-circle"
              :style="statusStyle(scope.data.row.status)"
            ></span>
            <span class="content-item">{{ showStatus(scope.data.row.status) }}</span>
            <el-link
              :underline="false"
              v-if="
                (scope.data.row.status == '03' || scope.data.row.status == '04') &&
                initiatorIdentityId != scope.data.row.orgOperatorId
              "
              :disabled="!hasPerm('IC_CHANGEORGOPERATORSTATUS_BTN')"
              icon="el-icon-edit"
              @click="showEditDialog(scope.data.row)"
            ></el-link>
          </div>
        </template>
        <template slot="roles" slot-scope="scope">
          <span> {{ scope.data.row.role || '-' }} </span>
        </template>
        <template slot="suspended" slot-scope="scope">
          {{ scope.data.row.verifySuspend ? 'Yes' : 'No' }}
        </template>
        <template slot="operation" slot-scope="scope">
          <el-button
            v-if="initiatorIdentityId != scope.data.row.orgOperatorId"
            type="text"
            size="small"
            class="btn"
            @click="handleDetail(scope.data.row)"
          >
            {{ $t('common.label.detail') }}
          </el-button>
          <span
            class="buttomBar"
            v-if="
              scope.data.row.status != '06' &&
              scope.data.row.status != '00' &&
              initiatorIdentityId != scope.data.row.orgOperatorId
            "
            >|</span
          >
          <el-button
            type="text"
            size="small"
            v-if="
              scope.data.row.status != '06' &&
              scope.data.row.status != '00' &&
              initiatorIdentityId != scope.data.row.orgOperatorId
            "
            class="btn"
            @click="handleCloseDialog(scope.data.row)"
          >
            {{ $t('common.button.close') }}
          </el-button>
        </template>
      </table-page>
    </div>
    <org-edit-status
      :editStatusVisible.sync="statusDialogVisible"
      :curStatus.sync="oldStatus"
      :receiverIdentityId.sync="receiverIdentityId"
      :newStatusList="newStatusList"
      :isShowReason="activeName == 'orgOperator' ? true : false"
      @updateList="updateList"
    ></org-edit-status>
    <template>
      <el-dialog
        :title="$t('IDC.orgOperator.closeOperatorTitle')"
        :visible.sync="closeOperatorVisible"
        width="448px"
        :before-close="closeOperator"
      >
        <el-form
          label-position="top"
          :model="closeOperatorForm"
          ref="closeOperatorForm"
          :rules="closeOperatorFormRules"
          class="add-form"
        >
          <div class="close-note">{{ closeNote }}</div>
          <el-divider></el-divider>
          <el-row>
            <p class="remark-reason">{{ $t('IDC.orgOperator.remarkAndReason') }}</p>
            <el-col :span="24">
              <el-form-item :label="$t('IDC.orgOperator.reason')" prop="reason">
                <el-select
                  size="small"
                  v-model="closeOperatorForm.reason"
                  :placeholder="$t('common.placeholder.select')"
                >
                  <el-option v-for="item in reasonList" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item :label="$t('IDC.orgOperator.remark')" prop="remark">
                <el-input
                  size="small"
                  type="textarea"
                  rows="4"
                  v-model="closeOperatorForm.remark"
                  :placeholder="$t('common.placeholder.enter')"
                >
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="closeOperator" size="small">{{ $t('common.button.cancel') }}</el-button>
          <el-button type="primary" size="small" @click="closeOperatorSubmit" v-preventReClick>{{
            $t('common.button.submit')
          }}</el-button>
        </span>
      </el-dialog>
    </template>
    <portal-short-code
      :searchVisible.sync="searchVisible"
      :short-code.sync="searchList.shortCode"
      :isSelect="true"
    ></portal-short-code>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import portalShortCode from '@/components/portal-short-code/index.vue';
import {
  getOrgOperatorList,
  closeOrgOperator,
  queryOrgOprRoleList,
  checkShortCode,
} from '@/views/identifier-center/api/api-organization-operator.js';
import {
  len6To16StrValidateObj,
  selectRequiredValidateObj,
  checkFuzzySearchInput,
  nonSpecialCharExceptPercentSignValidateObj,
  len0To18StrValidateObj,
  len0To32StrValidateObj,
  len0To30StrValidateObj,
  len24StrValidateObj,
} from '@/common/validator/common';
import OrgEditStatus from '../../pages/organization-operator/componts/edit-status.vue';
import cookie from '@/common/utils/cookie';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
export default {
  components: { PortalHeader, TablePage, PortalForm, portalShortCode, OrgEditStatus },
  name: 'OrgOperator',
  props: {
    activeName: {
      type: String,
      default: 'orgOperator',
    },
    propShortCode: {
      type: String,
      default: '',
    },
  },
  data() {
    let checkOrgShortCode = (rule, value, callback) => {
      if (!value) {
        return callback();
      }
      checkShortCode(this.searchList.shortCode)
        .then((res) => {
          if (res.header && res.header.success) {
            if (res.check === 'Exist') {
              callback();
            } else if (res.check === 'NotExist') {
              return callback(new Error('The Organization Short Code is not existed'));
            } else if (res.check === 'Invalid') {
              return callback(new Error('The Organization Short Code is not invalid'));
            }
          }
        })
        .catch(() => {
          return callback(new Error('The Organization Short Code Exception'));
        });
    };
    return {
      sortDataCode: '',
      sortCode: '',
      checkNull: false,
      // 下拉框数据
      todoInfoList: {
        sortData: [
          {
            label: 'Sort by',
            value: '',
          },
          {
            label: 'Dare of Birth',
            value: 'birthDay',
          },
          {
            label: 'First Name',
            value: 'firstName',
          },
          {
            label: 'Last Name',
            value: 'lastName',
          },
          {
            label: 'Middle Name',
            value: 'middleName',
          },
          {
            label: 'Organization Short Code',
            value: 'shortCode',
          },
          {
            label: 'User Name',
            value: 'userName',
          },
        ],
      },
      sort: [
        { label: 'Ascending', value: 'true' },
        { label: 'Descending', value: 'false' },
      ],
      sortBy: null,
      sortByMode: null,
      searchLoading: false,
      refreshTableFlag: false,
      searchVisible: false,
      isReset: false,
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      reasonList: [
        { label: 'Not Use', value: '1' },
        { label: 'Id Expired', value: '2' },
      ],
      searchList: {
        identityId: '',
        shortCode: this.propShortCode,
        operatorId: '%',
        status: '',
        operatorName: '',
        roles: '',
        username: '%',
        firstName: '%',
        middleName: '%',
        lastName: '%',
        birthday: '',
      },
      // 搜索字段记得修改
      sortArr: [
        'identityId',
        'orgOperatorId',
        'organizationCode',
        'operatorId',
        'status',
        'operatorName',
        'firstName',
        'middleName',
        'lastName',
        'dateBirth',
        'verifySuspend',
        'shortCode',
        'userName',
        'birthday',
      ],
      oldStatus: '',
      receiverIdentityId: '',
      statusDialogVisible: false,
      queryFormRules: {
        identityId: [nonSpecialCharExceptPercentSignValidateObj],
        operatorId: [nonSpecialCharExceptPercentSignValidateObj, len0To18StrValidateObj],
        operatorName: [nonSpecialCharExceptPercentSignValidateObj, len6To16StrValidateObj],
        shortCode: [
          nonSpecialCharExceptPercentSignValidateObj,
          { validator: checkOrgShortCode, trigger: 'blur' },
          len24StrValidateObj,
        ],
        username: [nonSpecialCharExceptPercentSignValidateObj, len0To32StrValidateObj],
        firstName: [nonSpecialCharExceptPercentSignValidateObj, len0To30StrValidateObj],
        middleName: [nonSpecialCharExceptPercentSignValidateObj, len0To30StrValidateObj],
        lastName: [nonSpecialCharExceptPercentSignValidateObj, len0To30StrValidateObj],
      },
      availableRoleList: [],
      closeOperatorVisible: false,
      closeNote: '',
      closeOperatorForm: {
        originalStatus: '',
        reason: '',
        remark: '',
        receiverIdentityId: '',
      },
      closeOperatorFormRules: {
        reason: [selectRequiredValidateObj],
        remark: [
          {
            required: true,
            message: 'Please enter content',
            trigger: 'blur',
          },
        ],
      },
      orgOperatorId: '',
      initiatorIdentityId: '',
      newStatusList: [],
    };
  },
  computed: {
    statusStyle() {
      return (status) => {
        switch (status) {
          case '03':
            return { 'background-color': '#658DF9' };
          case '06':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '07':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '00':
            return {
              'background-color': `${this.GlobalStyleVariables.backgroundColor}`,
              border: `1px solid ${this.GlobalStyleVariables.initialColor}`,
            };
          case '02':
            return {
              'background-color': `${this.GlobalStyleVariables.backgroundColor}`,
              border: `1px solid ${this.GlobalStyleVariables.initialColor}`,
            };
          case '04':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          case '05':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '08':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          default:
            return;
        }
      };
    },
    titleConfig() {
      if (this.activeName == 'organization') {
        return {
          title: this.$t('IDC.orgOperator.oragationTitle'),
          desc: this.$t('IDC.orgOperator.myOragationDesc'),
        };
      }
      if (this.activeName == 'myOrganization') {
        return {
          title: '',
          desc: this.$t('IDC.orgOperator.myOragationDesc'),
        };
      }
      return {
        title: this.$t('IDC.orgOperator.orgOperator'),
        desc: this.$t('IDC.orgOperator.myOragationDesc'),
      };
    },
    formConfigList() {
      if (this.activeName == 'organization' || this.activeName == 'myOrganization') {
        return [
          {
            label: this.$t('IDC.orgOperator.identityID'),
            key: 'identityId',
            comp: 'el-input',
            bind: {
              clearable: true,
              placeholder: this.$t('common.placeholder.enter'),
            },
          },
          {
            label: this.$t('IDC.orgOperator.operatorID'),
            key: 'operatorId',
            comp: 'el-input',
            bind: {
              clearable: true,
              placeholder: this.$t('common.placeholder.enter'),
            },
          },
          {
            label: this.$t('IDC.organizationOperatorDetail.label.Role'),
            key: 'roles',
            prop: 'roles',
            comp: 'el-select',
            options: this.availableRoleList,
            bind: {
              clearable: false,
              placeholder: this.$t('common.placeholder.select'),
            },
          },
          {
            label: this.$t('IDC.organizationOperatorDetail.label.IdentityStatus'),
            key: 'status',
            comp: 'el-select',
            bind: {
              placeholder: this.$t('common.placeholder.select'),
              id: 'status',
            },
            options: [
              { label: 'All', value: 'All' },
              { value: '03', label: 'Active' },
              { value: '00', label: 'New' },
              { value: '02', label: 'Pending Active' },
              { value: '04', label: 'Suspended' },
              { value: '05', label: 'Frozen' },
              { value: '06', label: 'Closed' },
              { value: '07', label: 'Capped' },
              { value: '08', label: 'Dormant' },
            ],
          },
          {
            label: this.$t('IDC.orgOperator.userName'),
            key: 'username',
            comp: 'el-input',
            bind: {
              clearable: true,
              placeholder: this.$t('common.placeholder.enter'),
            },
          },
        ];
      }
      return [
        {
          label: this.$t('IDC.orgOperator.identityID'),
          key: 'identityId',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          formItemClass: 'org-short-code',
          label: this.$t('IDC.orgOperator.orgShortCode'),
          key: 'shortCode',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: this.openVisible,
              },
            },
          ],
        },
        {
          label: this.$t('IDC.orgOperator.operatorID'),
          key: 'operatorId',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('IDC.organizationOperatorDetail.label.Role'),
          key: 'roles',
          prop: 'roles',
          comp: 'el-select',
          options: this.availableRoleList,
          bind: {
            clearable: false,
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          label: this.$t('common.label.status'),
          key: 'status',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            id: 'status',
          },
          options: [
            { label: 'All', value: 'All' },
            { label: 'Active', value: '03' },
            { label: 'Closed', value: '06' },
            { label: 'Dormant', value: '07' },
            { label: 'New', value: '00' },
            { label: 'Pending Active', value: '02' },
            { label: 'Suspended', value: '04' },
          ],
        },
        {
          label: this.$t('IDC.orgOperator.userName'),
          key: 'username',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: 'First Name',
          key: 'firstName',
          comp: 'el-input',
          bind: {
            id: 'firstName',
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: 'Middle Name',
          key: 'middleName',
          comp: 'el-input',
          bind: {
            id: 'middleName',
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: 'Last Name',
          key: 'lastName',
          comp: 'el-input',
          bind: {
            id: 'lastName',
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: 'Date of Birth',
          key: 'birthday',
          comp: 'el-date-picker',
          bind: {
            id: 'birthday',
            type: 'date',
            'value-format': 'yyyyMMdd',
            placeholder: 'Please select',
            format: 'dd-MM-yyyy',
          },
        },
      ];
    },
    columns() {
      if (this.activeName == 'organization') {
        return [
          {
            label: this.$t('IDC.orgOperator.identityID'),
            prop: 'orgOperatorId',
            align: 'left',
            'min-width': '185',
          },
          {
            label: this.$t('IDC.orgOperator.operatorID'),
            prop: 'operatorId',
            align: 'left',
            'min-width': '165',
          },
          {
            label: this.$t('IDC.organizationOperatorDetail.label.Role'),
            prop: 'role',
            align: 'left',
            'min-width': '200',
            __slotName: 'roles',
          },
          {
            label: this.$t('IDC.organizationOperatorDetail.label.IdentityStatus'),
            prop: 'status',
            align: 'left',
            'min-width': '165',
            __slotName: 'status',
          },
          {
            label: this.$t('IDC.orgOperator.userName'),
            prop: 'userName',
            align: 'left',
            'min-width': '135',
          },
          {
            label: this.$t('IDC.orgOperator.operation'),
            prop: 'operation',
            align: 'left',
            width: '165',
            __slotName: 'operation',
            fixed: 'right',
          },
        ];
      }
      if (this.activeName == 'myOrganization') {
        return [
          {
            label: this.$t('IDC.orgOperator.identityID'),
            prop: 'orgOperatorId',
            align: 'left',
            'min-width': '185',
          },
          {
            label: this.$t('IDC.orgOperator.operatorID'),
            prop: 'operatorId',
            align: 'left',
            'min-width': '165',
          },
          {
            label: this.$t('IDC.organizationOperatorDetail.label.Role'),
            prop: 'role',
            align: 'left',
            'min-width': '165',
            __slotName: 'roles',
          },
          {
            label: this.$t('IDC.organizationOperatorDetail.label.IdentityStatus'),
            prop: 'status',
            align: 'left',
            'min-width': '165',
            __slotName: 'status',
          },
          {
            label: this.$t('IDC.orgOperator.operation'),
            prop: 'operation',
            align: 'left',
            width: '165',
            __slotName: 'operation',
            fixed: 'right',
          },
        ];
      }
      return [
        {
          label: this.$t('IDC.orgOperator.identityID'),
          prop: 'identityId',
          align: 'left',
          'min-width': '185',
          'sort-method': (a, b) => {
            return a.identityId > b.identityId ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.orgShortCode'),
          prop: 'shortCode',
          align: 'left',
          'min-width': '250',
          'sort-method': (a, b) => {
            return a.shortCode > b.shortCode ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.operatorID'),
          prop: 'operatorId',
          align: 'left',
          'min-width': '165',
          'sort-method': (a, b) => {
            return a.operatorId > b.operatorId ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.status'),
          prop: 'status',
          align: 'left',
          'min-width': '165',
          __slotName: 'status',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.userName'),
          prop: 'userName',
          align: 'left',
          'min-width': '135',
          'sort-method': (a, b) => {
            return a.userName > b.userName ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.firstName'),
          prop: 'firstName',
          align: 'left',
          'min-width': '135',
          'sort-method': (a, b) => {
            return a.firstName > b.firstName ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.middleName'),
          prop: 'middleName',
          align: 'left',
          'min-width': '155',
          'sort-method': (a, b) => {
            return a.middleName > b.middleName ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.lastName'),
          prop: 'lastName',
          align: 'left',
          'min-width': '135',
          'sort-method': (a, b) => {
            return a.lastName > b.lastName ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.dateBirth'),
          prop: 'birthday',
          align: 'left',
          'min-width': '155',
          'sort-method': (a, b) => {
            return a.birthday > b.birthday ? 1 : -1;
          },
        },
        {
          label: this.$t('IDC.orgOperator.suspended'),
          prop: 'verifySuspend',
          align: 'left',
          'min-width': '135',
          'sort-method': (a, b) => {
            return a.verifySuspend > b.verifySuspend ? 1 : -1;
          },
          __slotName: 'suspended',
        },
        {
          label: this.$t('IDC.orgOperator.operation'),
          prop: 'operation',
          align: 'left',
          fixed: 'right',
          width: '165',
          __slotName: 'operation',
        },
      ];
    },
  },
  watch: {
    propShortCode(newval) {
      this.searchList.shortCode = newval;
    },
  },
  methods: {
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },
    updateList() {
      this.refreshTableFlag = true;
    },
    handleCommand(val) {
      this.sortBy = val;
      this.sortByMode = '';
      this.sortDataCode = '';
      this.checkNullFn();
      this.refreshTableFlag = true;
      this.paginationConfig.pageIndex = 1;
    },
    // 升序降序
    handleCommandSort(val) {
      this.sortByMode = val;
      this.checkNullFn();
      this.refreshTableFlag = true;
      this.paginationConfig.pageIndex = 1;
    },
    reset() {
      this.isReset = true;
      this.searchList = {
        identityId: '',
        shortCode: this.propShortCode,
        operatorId: '%',
        status: 'All',
        operatorName: '',
        roles: '',
        username: '%',
        firstName: '%',
        middleName: '%',
        lastName: '%',
        birthday: '',
      };
      this.$refs['searchList'].innerFormRef.resetFields();
      this.sortBy = '';
      this.sortByMode = '';
      this.sortDataCode = '';
      this.sortCode = '';
      this.refreshTableFlag = true;
      if (this.activeName === 'organization' || this.activeName === 'myOrganization') {
        this.refreshTableFlag = true;
        this.searchList.shortCode = this.propShortCode;
      }
    },
    checkNullFn() {
      if (
        this.searchList.operatorId === '' &&
        this.searchList.firstName === '' &&
        this.searchList.middleName === '' &&
        this.searchList.lastName === '' &&
        this.searchList.username === '' &&
        this.searchList.roles === ''
      ) {
        this.checkNull = true;
      } else {
        this.checkNull = false;
      }
    },
    search() {
      this.checkNullFn();
      this.searchLoading = true;
      this.refreshTableFlag = true;
      this.paginationConfig.pageIndex = 1;
      this.$refs['searchList'].innerFormRef.validate((valid) => {
        if (valid) {
          this.refreshTableFlag = true;
          this.paginationConfig.pageIndex = 1;
        } else {
          this.searchLoading = false;
          return false;
        }
      });
    },
    openVisible() {
      this.searchVisible = true;
    },
    addOrgOperator() {
      let path = '';
      path = '/mainPage/identifierCenter/organizationInfo/orgOperator/create';
      this.openNewWindow({ path: path });
    },
    handleDetail(data) {
      let params = {
        orgOperatorId: data.identityId,
        identityId: data.organizationId,
      };
      let path = '';
      path = '/mainPage/identifierCenter/organizationInfo/orgOperator/detail';
      this.openNewWindow({ path: path, query: { data: encodeURIComponent(JSON.stringify(params)) } });
    },
    showEditDialog(data) {
      this.getListNewStatus(data.status);
      this.statusDialogVisible = true;
      this.receiverIdentityId = data.orgOperatorId;
      this.statusCode = data.status;
      this.oldStatus = data.status;
    },
    getListNewStatus(status) {
      if (status === '03') {
        this.newStatusList = [
          {
            label: 'Suspended',
            value: '04',
          },
        ];
      } else if (status === '04') {
        this.newStatusList = [
          {
            label: 'Active',
            value: '03',
          },
        ];
      }
    },
    handleCloseDialog(scope) {
      this.closeOperatorVisible = true;
      this.closeNote = this.$t('IDC.orgOperator.closeNote', { obj: scope.orgOperatorId });
      this.orgOperatorId = scope.orgOperatorId;
      this.closeOperatorForm.originalStatus = scope.status;
      this.closeOperatorForm.receiverIdentityId = scope.orgOperatorId;
    },
    closeOperator() {
      this.closeOperatorVisible = false;
      this.$refs['closeOperatorForm'].resetFields();
    },
    closeOperatorSubmit() {
      this.$refs['closeOperatorForm'].validate((valid) => {
        if (valid) {
          let params = this.closeOperatorForm;
          closeOrgOperator(params).then((res) => {
            this.closeOperatorVisible = false;
            if (res.header && res.header.success) {
              let tip = this.$t('IDC.orgOperator.closeSuccessTip', { obj: this.orgOperatorId });
              this.$refs['closeOperatorForm'].resetFields();
              this.$message.success(tip);
              this.refreshTableFlag = true;
            }
          });
        }
      });
    },
    getTableData() {
      let params = {
        pageNo: this.paginationConfig.pageIndex,
        pageSize: this.paginationConfig.pageSize,
        shortCode: this.searchList.shortCode,
        operatorIdentityId: this.searchList.identityId,
        operatorId: this.searchList.operatorId,
        status: this.searchList.status,
        roleId: this.searchList.roles,
        firstName: this.searchList.firstName,
        middleName: this.searchList.middleName,
        lastName: this.searchList.lastName,
        operatorName: this.searchList.username,
        birthday: this.searchList.birthday,
      };
      if (this.sortBy || this.sortByMode) {
        // 排序
        params.sortingQueryResultsBy = this.sortBy || '';
        params.sortAcs = this.sortByMode || null;
      }
      let checkObj = {
        firstName: params.firstName,
        middleName: params.middleName,
        lastName: params.lastName,
        operatorName: params.operatorName,
        roleId: params.roleId,
        operatorId: params.operatorId,
      };
      if (!checkFuzzySearchInput(checkObj)) {
        this.$modal.msgError(
          'Specify at least one search condition of First Name, Middle Name, Last Name, Role, User Name, and Operator ID.'
        );
        this.refreshTableFlag = false;
        this.searchLoading = false;
        return;
      }
      return new Promise((resolve) => {
        if (!this.isReset) {
          getOrgOperatorList(params)
            .then((res) => {
              if (res.header && res.header.success) {
                res.list = res.list || [];
                res.totalRecords = Number(res.totalRecord) || 0;
                res.list.map((li) => {
                  li.orgOperatorId = li.identityId;
                });
                resolve(res);
              } else {
                res.list = [];
                res.totalRecords = 0;
                this.$message.error(res.header.responseDesc);
                resolve(res);
              }
            })
            .finally(() => {
              this.refreshTableFlag = false;
              this.searchLoading = false;
            });
        } else {
          resolve();
          this.isReset = false;
          this.refreshTableFlag = false;
          this.searchLoading = false;
        }
      });
    },
    async getRoleSelect() {
      let queryParams = {};
      queryParams.shortCode = this.searchList.shortCode;
      queryParams.roleType = '30';
      let res = await queryOrgOprRoleList(queryParams);
      if (res.header && res.header.success) {
        let arry = res.grantRoleList || [];
        this.availableRoleList = arry.map((item) => {
          return {
            value: item.roleCode,
            label: item.roleName,
          };
        });
        this.availableRoleList.unshift({ value: '', label: 'All' });
      }
    },
    // 原角色代码，t2后端改为直接渲染role，可能之后有问题，待测
    showRoleName(roles) {
      let roleArr = this.availableRoleList.filter((item) => {
        return roles.some((rs) => item.value === rs.roleId);
      });
      return roleArr.map((rm) => rm.label).join(',') || '-';
    },
  },
  created() {
    if (this.activeName == 'organization' || this.activeName == 'myOrganization') {
      this.getRoleSelect();
    } else {
      this.isReset = true;
      this.getRoleSelect();
      this.propShortCode = '';
    }
    this.searchList.status = 'All';
    this.searchList.roles = '';
  },
  mounted() {
    if (
      this.searchList.shortCode &&
      this.searchList.shortCode.startsWith('%') &&
      this.searchList.shortCode.length === 1
    ) {
      this.searchList = {
        identityId: '',
        shortCode: this.propShortCode,
        operatorId: '',
        msisdn: '',
        status: 'All',
        roles: '',
        username: '',
      };
      this.refreshTableFlag = true;
      this.isReset = false;
    } else if (this.searchList.shortCode) {
      this.searchList = {
        identityId: '',
        shortCode: this.propShortCode,
        operatorId: '',
        msisdn: '',
        status: 'All',
        roles: '',
        username: '',
      };
      this.isReset = false;
    }
    this.initiatorIdentityId = cookie.get('initiatorIdentityId');
  },
};
</script>

