import request from '@/common/utils/request';

export function getAccountTypeInfo() {
  return request({
    url: '/fintech/v1/Account/getAccountTypeInfo',
    method: 'get',
  });
}
export function queryAccount(data) {
  return request({
    url: '/fintech/v1/Account/queryAccount',
    method: 'post',
    data,
  });
}
export function modifyAccount(data) {
  return request({
    url: '/fintech/v1/Account/modifyAccount',
    method: 'post',
    data,
  });
}
export function getRuleProfile(data) {
  return request({
    url: '/fintech/v1/Account/getRuleProfile',
    method: 'post',
    data,
  });
}
export function exportAccountInfo(data) {
  return request({
    url: '/fintech/v1/Account/exportAccountInfo',
    method: 'post',
    data,
    responseType: 'blob',
  });
}
