<template>
  <div>
    <portal-header :class="isShowheaderTitle()" :projectObj="titleConfig"></portal-header>
    <div class="table-search-container">
      <el-divider class="header-divider" v-if="!isOrganization || activeName"></el-divider>
      <el-collapse-transition>
        <portal-form
          :class="isOrganization && !activeName ? 'till-portal-form' : ''"
          v-show="collapseValue"
          ref="tillForm"
          :needBottomLine="false"
          :form-data="tillForm"
          :form-rules="tillFormRules"
          :form-config-list="formConfigArray"
          :searchBtnLoading="searchLoading"
          @common-enter-event="searchTill"
          @reset-btn-click="reset"
          @search-btn-click="searchTill"
          :searchBtnDisabled="!hasPerm('IC_TILL_MANAGE_SEARCH_BTN')"
        />
      </el-collapse-transition>
    </div>
    <div class="till-table-list">
      <el-divider></el-divider>
      <el-button
        :disabled="!hasPerm('IC_TILL_ADDBASICINFO_NEXT_BTN')"
        @click="createTill"
        icon="el-icon-plus"
        size="small"
        type="primary"
      >
        {{ $t('common.button.create') }}
      </el-button>
      <table-page
        class="margin-top-16"
        size="small"
        :has-pagination="true"
        :isSearch="isSearch"
        :columns="isOrganization ? isOrgCokumns : tillColumns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :initializing-loading="fristSearchMap"
        :sortArr="['status', 'shortCode', 'msisdn', 'tillNumber']"
      >
        <template slot-scope="scope" slot="status">
          <div class="flex flex-align">
            <span
              v-if="scope.data.row.status !== ''"
              class="status-circle"
              :style="statusStyle(scope.data.row.status)"
            ></span>
            <span>{{ showStatus(scope.data.row.status) }}</span>
            <i
              v-if="scope.data.row.status == '03' || scope.data.row.status == '05'"
              id="language"
              class="el-icon-black-edit icon-cursor margin-left-12"
              @click="handleEditStatus(scope.data.row)"
            />
          </div>
        </template>
        <template slot-scope="scope" slot="operation">
          <el-button
            size="mini"
            type="text"
            class="table-operation-btn icon-cursor operation-button"
            @click="handleDetail(scope.data.row)"
          >
            {{ $t('common.button.detail') }}
          </el-button>
          <el-button
            v-if="scope.data.row.status == '03' || scope.data.row.status == '05'"
            size="mini"
            type="text"
            class="table-operation-btn icon-cursor"
            @click="showColose(scope.data.row)"
          >
            {{ $t('common.button.close') }}
          </el-button>
        </template>
      </table-page>
    </div>
    <close-dialog
      @ishow="ishow"
      :closeData="closeData"
      :closeDialog.sync="closeDialog"
      @closeSubmit="closeSubmit"
    ></close-dialog>
    <portal-short-code
      ref="selectShortCode"
      invoking-flag="createOrgOperator"
      :searchVisible.sync="searchVisible"
      :merchant-id.sync="tillFormData.merchantId"
      :short-code.sync="tillForm.orgshortcode"
      :isSelect="true"
    ></portal-short-code>
    <edit-status ref="editStatus" @closeStatusDialog="closeStatusDialog"></edit-status>
    <portal-add-popup
      id="popup-product"
      :isCheck="true"
      :isSingleCheck="true"
      :isCheckMemory="true"
      :hasCheckBox="false"
      :searchVisible.sync="addProductVisible"
      :addForm="addForm"
      :childSearch="loadProduct"
      @submit-call-back="selectProductList"
      :init-checks="initCheckList"
      :has-pagination="true"
    ></portal-add-popup>
  </div>
</template>

<script>
import { getSearchTillRules, tillListRules } from '@/views/identifier-center/validator/till.js';
import PortalHeader from '@/components/portal-header';
import PortalAddPopup from '@/components/portal-add-popup/index';
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import { selectTillList, getProduct } from '@/views/identifier-center/api/api-till';
import closeDialog from './components/close-dialog';
import portalShortCode from '@/components/portal-short-code/index.vue';
import EditStatus from '../view/components/edit-status.vue';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
import { checkFuzzySearchInput } from '@/common/validator/common.js';

const queryConditionSelectOptions = [
  { label: 'MSISDN', value: '0' },
  { label: 'Till Number + Organization Short Code', value: '1' },
  { label: 'Product', value: '2' },
  { label: 'Status', value: '3' },
];

export default {
  name: 'TillList',
  components: {
    TablePage,
    PortalHeader,
    PortalForm,
    closeDialog,
    portalShortCode,
    EditStatus,
    PortalAddPopup,
  },
  props: {
    activeName: {
      type: Boolean,
      default: false,
    },
    isOrganization: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      fristSearch: false,
      // 定义一个isOrganizationPage来接收传递过来的值
      isOrganizationPage: this.isOrganization,
      queryConditionSelectOptions,
      searchVisible: false,
      tillFormData: {
        orgshortcode: '',
        merchantId: '',
      },
      addProductVisible: false,
      closeData: {
        identityId: '',
        tillNumber: '',
      },
      closeDialog: false,
      total: 0,
      pageNo: 1,
      pageSize: 10,
      tillForm: {
        msisdn: '%',
        tillNumber: '%',
        orgshortcode: '',
        status: 'All',
        productName: '',
        queryCondition: '0',
      },
      addForm: {
        title: this.$t('IDC.till.selectProduct'),
        label: '',
        idKey: 'productIndex',
        key: 'till',
        columns: [
          { label: this.$t('IDC.till.productName'), prop: 'productName', align: 'left' },
          { label: this.$t('IDC.till.description'), prop: 'description', align: 'left' },
        ],
        searchForm: {
          formData: {
            producrName: '',
          },
          formConf: [
            {
              label: this.$t('IDC.till.productName'),
              key: 'producrName',
              prop: 'producrName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
        },
      },
      searchLoading: false,
      tills: [],
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      initCheckList: [],
      productIndex: '',
      isOrgCokumns: [
        {
          'sort-method': (a, b) => {
            return a.tillNumber > b.tillNumber ? 1 : -1;
          },

          label: this.$t('IDC.till.tillNumber'),
          prop: 'tillNumber',
          align: 'left',
        },
        {
          'sort-method': (a, b) => {
            return a.msisdn > b.msisdn ? 1 : -1;
          },
          label: this.$t('IDC.till.msisdn'),
          prop: 'msisdn',
          align: 'left',
        },
        {
          label: this.$t('common.label.status'),
          prop: 'status',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
          align: 'left',
          __slotName: 'status',
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: 200,
        },
      ],
      tillColumns: [
        {
          'sort-method': (a, b) => {
            return a.shortCode > b.shortCode ? 1 : -1;
          },
          label: this.$t('IDC.till.organizationShortCode'),
          prop: 'shortCode',
          align: 'left',
        },
        {
          'sort-method': (a, b) => {
            return a.tillNumber > b.tillNumber ? 1 : -1;
          },

          label: this.$t('IDC.till.tillNumber'),
          prop: 'tillNumber',
          align: 'left',
        },
        {
          'sort-method': (a, b) => {
            return a.organizationName > b.organizationName ? 1 : -1;
          },

          label: this.$t('IDC.till.organizationName'),
          prop: 'organizationName',
          align: 'left',
        },
        {
          'sort-method': (a, b) => {
            return a.msisdn > b.msisdn ? 1 : -1;
          },
          label: this.$t('IDC.till.msisdn'),
          prop: 'msisdn',
          align: 'left',
        },
        {
          label: this.$t('common.label.status'),
          prop: 'status',
          'sort-method': (a, b) => {
            return a.status > b.status ? 1 : -1;
          },
          align: 'left',
          __slotName: 'status',
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          fixed: 'right',
          __slotName: 'operation',
          width: '160',
        },
      ],
      isSearch: false,
      collapseValue: true,
      hasPagination: true,
      titleConfig: {
        title: this.$t('IDC.till.pageTitle'),
        desc: this.$t('IDC.till.createTitle'),
      },
    };
  },
  watch: {
    'tillForm.orgshortcode': {
      handler(value) {
        if (!value) {
          this.tillFormData = {
            orgshortcode: '',
            merchantId: '',
          };
        }
      },
    },
    'tillForm.productName': {
      handler(value) {
        if (!value) {
          this.productIndex = '';
        }
      },
    },
    isOrganization(value) {
      this.isOrganizationPage = value;
    },
  },
  computed: {
    statusOptions() {
      let list = [
        { label: 'Active', value: '03' },
        { label: 'Closed', value: '06' },
        { label: 'Frozen', value: '05' },
        { label: 'New', value: '00' },
        { label: 'Pending Active', value: '02' },
      ];
      if (this.isOrganizationPage) {
        list.unshift({ label: 'All', value: 'All' });
      }
      return list;
    },
    tillFormRules() {
      if (this.isOrganizationPage) {
        return getSearchTillRules();
      } else {
        return tillListRules();
      }
    },
    fristSearchMap() {
      if (this.isOrganizationPage && this.activeName) {
        return true;
      }
      return false;
    },
    formConfigArray() {
      let formList = [
        {
          hidden: this.isOrganizationPage,
          label: 'Query Condition',
          key: 'queryCondition',
          comp: 'el-select',
          required: true,
          options: this.queryConditionSelectOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
          event: {
            change: (e) => {
              this.tillForm = {
                msisdn: '%',
                tillNumber: '%',
                orgshortcode: '',
                status: e === '3' ? '03' : 'All',
                productName: '',
                queryCondition: e,
              };
            },
          },
        },
        {
          hidden: this.isShowItem('1'),
          label: this.$t('IDC.till.tillNumber'),
          key: 'tillNumber',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'tillNumber',
          },
        },
        {
          hidden: this.isShowItem('0'),
          label: this.$t('IDC.till.msisdn'),
          key: 'msisdn',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            id: 'msisdn',
          },
        },
        {
          hidden: this.isShowItem('2'),
          formItemClass: 'org-short-code',
          label: this.$t('IDC.till.product'),
          key: 'productName',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.addProduct();
                },
              },
            },
          ],
          event: {
            focus: this.addProduct,
          },
        },
        {
          hidden: this.isShowItem('3'),
          label: this.isOrganizationPage ? this.$t('common.label.status') : 'Identity Status',
          key: 'status',
          comp: 'el-select',
          options: this.statusOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          hidden: this.isShowItem('1', false),
          formItemClass: 'org-short-code',
          label: this.$t('IDC.till.organizationShortCode'),
          key: 'orgshortcode',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.searchVisible = true;
                },
              },
            },
          ],
        },
      ];
      return formList;
    },
    statusStyle() {
      return (status) => {
        switch (status) {
          case '03':
            return { 'background-color': this.GlobalStyleVariables.statusNoticeColor };
          case '06':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case '05':
            return { 'background-color': this.GlobalStyleVariables.statusColorwarning };
          case '04':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          case '07':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case '00':
          case '02':
            return `background-color:${this.GlobalStyleVariables.backgroundColor};border:1px solid ${this.GlobalStyleVariables.initialColor};`;
          default:
            return;
        }
      };
    },
  },
  methods: {
    isShowItem(val, identification = true) {
      let showList = ['1', '2'];
      if (this.isOrganizationPage) {
        if (!val.includes(showList) && identification) {
          return false;
        }
        return true;
      } else if (this.tillForm.queryCondition === val) {
        return false;
      } else {
        return true;
      }
    },
    isShowheaderTitle() {
      this.fristSearch = this.isOrganizationPage;
      let val = '';
      if (this.isOrganizationPage) {
        val = 'till-page-header';
        this.$set(this.titleConfig, 'desc', this.$t('IDC.till.detailTile'));
      }
      if (this.activeName) {
        this.isOrganizationPage = true;
        this.$set(this.titleConfig, 'title', this.$t('IDC.till.tillList'));
        this.$set(this.titleConfig, 'desc', this.$t('IDC.till.detailTile'));
        val = '';
      }
      return val;
    },
    selectProductList(val) {
      this.productIndex = val.productIndex;
      this.tillForm.productName = val.productName;
    },
    async loadProduct(params) {
      let data = {
        pageSize: params.pageSize || 10,
        pageNo: params.pageNo || 1,
        pageQuery: true,
      };
      if (params.searchKey) {
        data.productName = params.searchKey.producrName;
      }
      let res = await getProduct(data);
      return res;
    },
    addProduct() {
      this.addProductVisible = true;
    },
    closeStatusDialog() {
      this.getList();
    },
    getList() {
      this.isSearch = true;
    },
    handleEditStatus(val) {
      let params = val;
      params.identityId = val.tillId;
      this.$refs['editStatus'].showView(params);
    },
    showColose(scope) {
      this.closeData = {
        identityId: scope.tillId,
        tillNumber: scope.tillNumber,
      };
      this.closeDialog = true;
    },
    ishow() {
      this.closeDialog = false;
    },
    closeSubmit() {
      this.isSearch = true;
    },
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },
    reset() {
      this.$refs['tillForm'].innerFormRef.resetFields();
      this.isSearch = true;
    },
    searchTill() {
      let { queryCondition, tillNumber, orgshortcode } = this.tillForm;
      let checkObj = {
        tillNumber,
        orgshortcode,
      };
      if (queryCondition === '1' && !checkFuzzySearchInput(checkObj)) {
        this.$message({ message: this.$t('IDC.till.listTips'), type: 'warning' });
        return;
      }
      this.searchLoading = true;
      this.$refs['tillForm'].innerFormRef.validate((valid) => {
        if (valid) {
          this.isSearch = true;
        } else {
          this.searchLoading = false;
          return false;
        }
      });
    },
    handleDetail(row) {
      let params = {
        title: row.tillNumber,
        tillId: row.tillId,
      };
      this.openNewWindow({
        path: '../../organizationInfo/till/view',
        query: { data: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    async createTill() {
      this.openNewWindow({ path: '../../organizationInfo/till/create' });
    },
    getTableData() {
      if (!this.hasPerm('IC_TILL_MANAGE_SEARCH_BTN')) {
        return;
      }
      return new Promise((resolve) => {
        let { queryCondition, status } = this.tillForm;
        if (queryCondition !== '3' && !this.isOrganizationPage) {
          status = '';
        }
        let params = {
          pageIndex: this.paginationConfig.pageIndex,
          pageSize: this.paginationConfig.pageSize,
          msisdn: this.tillForm.msisdn === '%' ? '' : this.tillForm.msisdn,
          tillNumber: this.tillForm.tillNumber === '%' ? '' : this.tillForm.tillNumber,
          status: status,
          productId: this.productIndex,
          shortCode: this.tillForm.orgshortcode || '',
        };
        selectTillList(params)
          .then((res) => {
            if (res.header && res.header.success) {
              if (res.list) {
                res.totalRecords = res.totalRecord;
              } else {
                res.totalRecords = 0;
                res.list = [];
              }
              resolve(res);
            }
          })
          .catch(() => {})
          .finally(() => {
            this.searchLoading = false;
            this.isSearch = false;
          });
      });
    },
  },
};
</script>

<style scoped lang="scss">
/deep/ .org-short-code {
  position: relative;
  .el-input__inner {
    border-radius: 4px;
    padding-right: 76px;
  }
  .el-input__suffix {
    transform: translateX(-20px) !important;
  }
}
.table-search-container {
  box-shadow: none;
}
/deep/.till-page-header {
  min-height: 60px;
  .page-title {
    display: none;
  }
}
/deep/ .page-header {
  border-bottom: none;
}
/deep/.till-list-divider {
  width: 90%;
  margin: auto;
}
.till-table-list {
  background-color: $--text_color--white;
  padding: 0 20px 20px 20px;
}
/deep/.el-divider--horizontal {
  margin: 0 0 20px 0;
}
/deep/ .header-divider {
  margin-bottom: 0;
}
.till-portal-form {
  padding-top: 0;
}
/deep/.el-dialog__footer {
  margin-top: 20px;
}
</style>
