<template>
  <div class="my-organization-container">
    <section class="container-top">
      <div class="info-wrapper">
        <span class="avatar">
          <i class="el-icon-Organization"></i>
        </span>
        <div class="user-info">
          <el-descriptions :title="organizationName">
            <el-descriptions-item label="Identity Status">
              {{ identityStatus }}
              <!--      v-if="false" 暂时在该页面不支持修改状态        -->
              <svg-icon @click="identityEdit" v-if="false" icon-class="edit" class="edit-icon"></svg-icon>
            </el-descriptions-item>
            <el-descriptions-item label="Registration Date"
              >{{ registrationDate | localTimeFormat('DD/MM/YYYY') }}
            </el-descriptions-item>
          </el-descriptions>
        </div>
      </div>
      <div class="module-btn">
        <button @click="toTransactions" v-if="false">
          <svg-icon icon-class="transactions" class="module-btn_icon"></svg-icon> Transactions
        </button>
        <button @click="toChildOrganization">
          <svg-icon icon-class="childOrganization" class="module-btn_icon"></svg-icon> Child Organization
        </button>
        <button @click="toViewDetail">
          <svg-icon icon-class="toVisible" class="module-btn_icon"></svg-icon> View Detail
        </button>
      </div>
    </section>

    <section class="module-page">
      <el-tabs v-model="activeName">
        <el-tab-pane :lazy="true" label="Account" name="account" v-if="hasPerm('IC_OPERATOR_ORG_SEARCH_BTN')">
          <org-account :identityId="organizationId" :isTopOrganization="isTopOrganization"></org-account>
        </el-tab-pane>
        <el-tab-pane :lazy="true" label="Bank Account" name="bankAcount" v-if="hasPerm('IC_OPERATOR_ORG_SEARCH_BTN')">
          <bank-account :identity-id="organizationId"></bank-account>
        </el-tab-pane>

        <el-tab-pane :lazy="true" label="Operator" name="operator" v-if="hasPerm('IC_OPERATOR_ORG_SEARCH_BTN')">
          <operator :activeName="`myOrganization`" :propShortCode="shortCode"></operator>
        </el-tab-pane>

        <el-tab-pane
          :lazy="true"
          label="Till"
          name="till"
          v-if="hasPerm('IC_TILL_MANAGE_SEARCH_BTN') || hasPerm('IC_TILL_ADDBASICINFO_NEXT_BTN')"
        >
          <till :isOrganization="true"></till>
        </el-tab-pane>
      </el-tabs>
    </section>

    <portal-dialog
      :visible="dialogVisible"
      :title="identityDialogTitle"
      @closed-dialog="editIdentityCancel"
      @cancel-btn-click="editIdentityCancel"
      @confirm-btn-click="editIdentityConfirm"
    >
      <div slot="content" class="edit-identity">
        <span class="sub-title">{{ subtitle }}</span>
        <span class="active">{{ identityStatus }}</span>

        <el-form ref="editIdentityForm" :model="editIdentityForm" :rules="editIdentityRules">
          <el-form-item label="New Identity Status" prop="newIdentity">
            <el-select v-model="editIdentityForm.newIdentity" placeholder="Please select">
              <el-option
                v-for="(item, index) in statusOptions"
                :key="index"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-divider></el-divider>

          <el-form-item label="Remark" prop="remark" class="margin-bottom-0">
            <el-input type="textarea" rows="4" v-model="editIdentityForm.remark"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </portal-dialog>
  </div>
</template>

<script>
import BankAccount from '@/views/identifier-center/pages/bank-account/organization-bank-account';
import Till from '../till/list';
import Operator from '@/views/identifier-center/pages/organization-operator';
import OrgAccount from '@/views/identifier-center/pages/account';
import {
  queryOrganizationDetail,
  queryOrganizationStatusList,
  changeOrganizationStatus,
} from '../../api/my-organization';
import { inputRequiredValidateObj } from '@/common/validator/common';
import PortalDialog from '@/components/portal-dialog';
import cookie from '@/common/utils/cookie';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';

export default {
  name: 'MyOrganization',
  components: { PortalDialog, OrgAccount, BankAccount, Till, Operator },
  data() {
    return {
      activeName: 'account',
      organizationName: '-',
      identityStatus: '-',
      statusCode: '',
      registrationDate: [],
      dialogVisible: false,
      identityDialogTitle: 'Edit Identity Status',
      subtitle: 'Current Identity Status',
      editIdentityForm: {
        newIdentity: '',
        remark: '',
      },
      editIdentityRules: {
        newIdentity: [inputRequiredValidateObj],
        remark: [inputRequiredValidateObj],
      },
      statusOptions: [],
      organizationId: '',
      isTopOrganization: false,
      shortCode: '',
    };
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      queryOrganizationDetail({ identityId: '' }).then((res) => {
        if (res.header && res.header.success) {
          let organizationInfo = res.organizationInfo;
          if (organizationInfo) {
            this.organizationName = organizationInfo.shortCode + ' - ' + organizationInfo.organizationName;
            this.statusCode = organizationInfo.status;
            this.identityStatus = this.showIdentityStatus(this.statusCode);
            this.registrationDate = organizationInfo.createTime;
            this.organizationId = organizationInfo.organizationId;
            this.isTopOrganization = organizationInfo.isTopOrganization;
            this.shortCode = organizationInfo.shortCode;
            cookie.set('organizationIdentityId', organizationInfo.organizationId);
          }
        }
      });
    },

    toChildOrganization() {
      this.openNewWindow({
        path: '/mainPage/identifierCenter/organizationInfo/organization/detail',
        query: {
          data: encodeURIComponent(
            JSON.stringify({
              activeName: 'children',
              organizationId: this.organizationId,
              identityId: this.organizationId,
              propShortCode: this.shortCode,
            })
          ),
        },
      });
    },

    toTransactions() {
      this.openNewWindow({
        path: '/mainPage/identifierCenter/organizationInfo/organization/detail',
        query: {
          data: encodeURIComponent(
            JSON.stringify({
              activeName: 'reviewTransaction',
              organizationId: this.organizationId,
              identityId: this.organizationId,
              propShortCode: this.shortCode,
            })
          ),
        },
      });
    },

    toViewDetail() {
      this.openNewWindow({
        path: '/mainPage/identifierCenter/organizationInfo/organization/detail',
        query: {
          data: encodeURIComponent(
            JSON.stringify({
              activeName: 'organizationInfo',
              organizationId: this.organizationId,
              identityId: this.organizationId,
              propShortCode: this.shortCode,
            })
          ),
        },
      });
    },

    showIdentityStatus(val) {
      return STATUS_MAP[val] || '-';
    },

    identityEdit() {
      this.dialogVisible = true;
      this.getStatusOptions();
    },

    getStatusOptions() {
      let data = {
        statusCode: this.statusCode,
      };
      queryOrganizationStatusList(data).then((res) => {
        if (res.header && res.header.success) {
          this.statusOptions = res.statusList;
        }
      });
    },

    editIdentityCancel() {
      this.$refs['editIdentityForm'].resetFields();
      this.dialogVisible = false;
    },

    editIdentityConfirm() {
      this.$refs['editIdentityForm'].validate((valid) => {
        if (valid) {
          this.changeStatus();
        }
      });
    },

    changeStatus() {
      let data = {
        newStatus: this.editIdentityForm.newIdentity,
        oldStatus: this.statusCode,
        receiverIdentityId: this.organizationId,
        remark: this.editIdentityForm.remark,
      };
      changeOrganizationStatus(data)
        .then((res) => {
          if (res.header && res.header.success) {
            this.$message.success('Operation succeeded.');
            this.init();
            this.dialogVisible = false;
          } else {
            this.$message.error(res.header.responseDesc || 'Operation failed!');
          }
        })
        .catch((err) => {
          this.$message.error(err.message);
        });
    },
  },
};
</script>

