<template>
  <el-dialog
    :title="$t('IDC.orgOperator.editStatus')"
    :visible.sync="editStatusVisible"
    width="448px"
    :before-close="closeEdit"
  >
    <el-form label-position="top" :model="editForm" ref="editForm" :rules="editFormRules" class="add-form">
      <el-row :gutter="20">
        <el-col :span="24">
          <el-form-item :label="$t('IDC.orgOperator.currentStatus')" prop="oldStatus">
            <div class="text">
              <span class="text" v-text="showStatus(editForm.oldStatus)" />
            </div>
          </el-form-item>
        </el-col>
        <el-col :span="24" class="new-status">
          <el-form-item :label="$t('IDC.orgOperator.newStatus')" prop="newStatus">
            <el-select size="small" v-model="editForm.newStatus" :placeholder="$t('common.placeholder.select')">
              <el-option v-for="item in newStatusList" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <div v-if="isShowReason" class="remark-reason">{{ $t('IDC.orgOperator.remarkAndReason') }}</div>
        </el-col>
        <el-col :span="24" v-if="isShowReason">
          <el-form-item :label="$t('IDC.orgOperator.reason')" prop="reasonId">
            <el-select size="small" v-model="editForm.reasonId" :placeholder="$t('common.placeholder.select')">
              <el-option v-for="item in reasonList" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item :label="$t('IDC.orgOperator.remark')" prop="remark">
            <el-input
              size="small"
              type="textarea"
              rows="4"
              v-model="editForm.remark"
              :placeholder="$t('common.placeholder.enter')"
            >
            </el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>

    <span slot="footer" class="dialog-footer">
      <el-button @click="closeEdit" size="small">{{ $t('common.button.cancel') }}</el-button>
      <el-button :loading="submitLoading" type="primary" size="small" @click="editSubmit" v-preventReClick>{{
        $t('common.button.submit')
      }}</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { changeStatus } from '@/views/identifier-center/api/api-organization-operator.js';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
export default {
  name: 'OrgEditStatus',
  props: {
    editStatusVisible: {
      type: Boolean,
      default: false,
    },
    curStatus: {
      type: String,
      default: () => '',
    },
    receiverIdentityId: {
      type: String,
      default: () => '',
    },
    currentPage: {
      type: String,
      default: () => '',
    },
    isShowReason: {
      type: Boolean,
      default: true,
    },
    newStatusList: {
      type: Array,
      default: () => [],
    },
    submitLoading: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      editForm: {
        oldStatus: '',
        newStatus: '',
        reasonId: '',
        remark: '',
        receiverIdentityId: '',
      },
      reasonList: [
        { label: 'Not Use', value: '1' },
        { label: 'Id Expired', value: '2' },
      ],
      editFormRules: {
        newStatus: [
          {
            required: true,
            message: 'Please select content',
            trigger: 'change',
          },
        ],
        remark: [
          {
            required: true,
            message: 'Please enter remark',
            trigger: 'blur',
          },
        ],
      },
      operatorId: '',
    };
  },
  watch: {
    curStatus: {
      handler(value) {
        this.editForm.oldStatus = value;
      },
    },
    receiverIdentityId: {
      handler(value) {
        this.editForm.receiverIdentityId = value;
      },
    },
  },
  created() {},
  methods: {
    showStatus(val) {
      return STATUS_MAP[val] || '-';
    },

    closeEdit() {
      this.$emit('update:editStatusVisible', false);
      this.$refs['editForm'].resetFields();
    },
    editSubmit() {
      this.$refs['editForm'].validate((valid) => {
        if (valid) {
          let params = this.editForm;
          changeStatus(params)
            .then((res) => {
              this.$emit('update:editStatusVisible', false);
              if (res.header && res.header.success) {
                if (this.currentPage == 'detail' && !res.taskId) {
                  this.$emit('update:curStatus', this.editForm.newStatus);
                }
                this.$emit('update:editStatusVisible', false);
                this.$emit('updateList');
                this.$refs['editForm'].resetFields();
                if (res.taskId) {
                  this.$message.success('The request must be approved by another operator.');
                } else {
                  this.$message.success('Submit change request success!');
                  this.refreshCurrentPage();
                }
              }
            })
            .catch(() => {
              this.$emit('update:editStatusVisible', false);
            });
        }
      });
    },
  },
};
</script>

<style scoped lang="scss">
.text {
  line-height: 19px;
}
.remark-reason {
  font-size: 14px;
  color: $--text_color--strong;
  font-weight: bold;
  margin-bottom: 20px;
}
</style>
