// send sms下拉框
const BASE_PATH = '/fintech/v1/notification_type';
// 查询通知模板列表
export function getNotificationTypeList(data) {
  return request({
    url: buildUrlWithObj(`${BASE_PATH}/list`, data),
    method: 'get',
  });
}
// 查询通知模板信息
export function getNotificationTypeInfo(typeId) {
  return request({
    url: `${BASE_PATH}/queryInfo?typeId=${typeId}`,
    method: 'get',
  });
}
export function sendNotification(data) {
  return request({
    url: '/fintech/v1/notification/send',
    method: 'post',
    data,
  });
}
