<template>
  <el-dialog
    :visible="isShow"
    :title="$t('IDC.till.closeTillTitle')"
    width="448px"
    top="29vh"
    height="342"
    :close-on-click-modal="false"
    @submit.prevent.native
    :before-close="closeService"
  >
    <h1 class="dialog-title">{{ $t('IDC.till.closeTill') + ' ' + closeData.tillNumber + ' ' + '?' }}</h1>
    <el-divider />
    <el-form :rules="rules" label-position="top" ref="tillDialog" :model="submitData">
      <el-form-item label="Remark" prop="remark">
        <el-input type="textarea" :rows="5" resize="none" v-model="submitData.remark" placeholder="Please enter " />
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button class="btn-cancel" @click="closeService()">{{ $t('common.button.cancel') }}</el-button>
      <el-button type="primary" :loading="submitLoading" size="small" @click="submitService()" v-preventReClick>{{
        $t('common.button.confirm')
      }}</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { closeTill } from '@/views/identifier-center/api/api-till';
import { inputRequiredValidateObj } from '@/common/validator/common';
export default {
  name: 'closeDialog',
  props: {
    closeDialog: {
      type: Boolean,
      default: false,
    },
    closeData: {
      type: Object,
    },
  },
  data() {
    return {
      rules: {
        remark: [inputRequiredValidateObj],
      },
      submitLoading: false,
      isShow: this.closeDialog,
      submitData: {
        remark: '',
      },
    };
  },
  watch: {
    closeDialog(val) {
      this.isShow = val;
    },
  },
  computed: {},
  methods: {
    closeService() {
      this.$refs.tillDialog.resetFields();
      this.isShow = false;
      this.$emit('ishow', false);
    },
    submitService() {
      this.$refs['tillDialog'].validate((valid) => {
        if (valid) {
          let params = {
            receiverIdentityId: this.closeData.identityId,
            remark: this.submitData.remark,
          };
          this.submitLoading = true;
          closeTill(params).then((res) => {
            if (res.header && res.header.success) {
              this.$modal.msgSuccess(`Till "${this.closeData.tillNumber}" closed successfully.`);
              this.closeService();
              this.$emit('closeSubmit');
            } else {
              this.closeService();
            }
            this.submitLoading = false;
          });
        }
      });
    },
  },
  mounted() {},
};
</script>
<style scoped lang="scss">
.dialog-title {
  color: $--text_color--emphasis;
  font-size: 14px;
}
</style>
